body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: sans-serif;
  overflow: hidden;
}

.App {
  height: 100vh;
  width: 100%;
  background: url(/react-weather-app/static/media/bg1.a9a68726.jpg) no-repeat center/cover;
  overflow: hidden;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: center;
  -webkit-justify-content: center;
  align-items: center;
  -webkit-align-items: center;
}

form {
  padding: 0 10px;
  box-sizing: border-box;
  position: relative;
  width: 100%;
  max-width: 500px;
  display: flex;
  display: -webkit-flex;
  justify-content: flex-start;
  -webkit-justify-content: flex-start;
}

.input {
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  font-size: 25px;
  padding: 10px 35px;
  border: none;
  border-radius: 8px;
  outline: none;
  color: #111;
  box-shadow: 0px 2px 6px #00000060;
  -webkit-appearance: none;
  -webkit-transition: 200ms;
  transition: 200ms;
}

.input:focus {
  box-shadow: 0px 5px 8px #00000050;
  width: calc(100% - 80px);
}

form .input:focus + button[type="submit"]{
  opacity: 1;
  z-index: 1;
}

form button[type="submit"]{
  right: 10px;
  top: 0;
  position: absolute;
  opacity: 0;
  z-index: -1;
  border: none;
  background-color: transparent;
  min-width: 50px;
  height: 100%;
  outline: none;
  border-radius: 50%;
  font-size: 30px;
  color: #fff;
  text-shadow: 1px 1px 3px #00000078;
  -webkit-transition: 200ms;
  transition: 200ms;
}

.input::-webkit-input-placeholder {
  color: #00000052;
  text-align: center;
}

.input::-ms-input-placeholder {
  color: #00000052;
  text-align: center;
}

.input::placeholder {
  color: #00000052;
  text-align: center;
}


.result {
  margin: 40px 0 0;
  min-height: 50px;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
}

.data {
  font-size: 25px;
  margin: 0 15px;
  line-height: 30px;
  height: 30px;
}

.data img {
  height: 100%;
}


@media only screen and (max-width: 325px){

  .result {
    flex-direction: column;
    min-height: 160px;
  }
  .result .data {
    margin: 5px 0;
  }
}
